<template>
    <div style="margin:2% 10%;">
        <div style="margin: 20px 0;padding-left:20px;width:20%;">
        <el-divider content-position="left"><span style = "font-size:20px;font-weight:bolder;color:grey;"></span></el-divider>
        <el-input type="text" placeholder="" v-model="title" maxlength="20" show-word-limit >
        </el-input>
        </div>


        <div style="margin: 20px 0;padding-left:20px; width:30%;">
        <el-divider content-position="left" style = "font-size:15px;font-weight:bolder;color:grey;"></el-divider>
        <el-input type="textarea" placeholder="" v-model="info" maxlength="30" show-word-limit>
        </el-input>
        </div>
        <mavon-editor style="min-height: 600px" ref=md v-model="markdown" :language="language"  @imgAdd="imgAdd" @imgDel="imgDel" @save="saveDoc"></mavon-editor>
        <div style="margin: 20px 0;padding-left:20px;width:20%;">
            <el-divider content-position="left" style = "font-size:15px;font-weight:bolder;color:grey;"></el-divider>
            <el-input type="text" placeholder="';'" v-model="tag" maxlength="30" show-word-limit >
            </el-input>
        </div>
        <el-row>
            <el-button style="margin-top:30px;" round  @click="uploadDoc"></el-button>
            <el-button type="success" style="margin-top:30px;" round></el-button>
        </el-row>
    </div>
</template>
<script>
// Local Registration
import { mavonEditor } from 'mavon-editor'
import 'mavon-editor/dist/css/index.css'
import * as API from "@/api/article/";
export default {
    name: 'createarticlepage',
    components: {
        mavonEditor
    },
    data(){
        return {
            title: '',
            info: '',
            img_file : {},
            markdown : "",
            tag:"",
            language : "zh-CN",

            // subfield : false,
        }
    },
    methods: {
        // @imgAdd event
        imgAdd(pos, $file){
            // ..
            var formdata = new FormData();
            formdata.append('image', $file);
            this.img_file[pos] = $file;
            this.$axios({
                url: '/api/v1/uploadimage',
                method: 'post',
                data: formdata,
                headers: { 'Content-Type': 'multipart/form-data'},
            }).then((url) => {
                // .url![...](0) -> ![...](url)
                /**
                * $vm mavonEditor
                * 1. : `import {mavonEditor} from ...` `$vm``mavonEditor`
                * 2. $refs: htmlref : `<mavon-editor ref=md ></mavon-editor>`$vm` `this.$refs.md`
                */
                this.$refs.md.$img2Url(pos, url.data.msg);
            })
        },

        imgDel(pos) {
            delete this.img_file[pos];
        },

        // updateDoc(markdown, text) {
        //     console.log("markdown: " + markdown);
        //     console.log("html:" + text);
        // },

        saveDoc(markdown, text) {
            let _this = this;
            let obj = {
                title:_this.title,
                info: _this.info,
                markdown: markdown,
            };
            console.log(obj);
            console.log(text);
            API.createDoc(obj
                )
                .then(
                response => {
                    console.log(response)
                    _this.$message({
                    message: "",
                    type: "success"
                    });
                window.location.href="/articlepage/"+response.data.id
                },
                response => console.log("" + response)
                );
        },
        
        uploadDoc() {
            let _this = this;
            let obj = {
                title:_this.title,
                info: _this.info,
                markdown :_this.markdown,
            };
            console.log(obj);
            API.createDoc(obj
                )
                .then(
                response => {
                    console.log(response)
                    _this.$message({
                    message: "",
                    type: "success"
                    });
                window.location.href="/articlepage/"+response.data.id
                },
                response => console.log("" + response)
                );
        },
    }
}
</script>
